import React, { useEffect } from "react";
import { Button, Form, Input, message, Upload } from "antd";
import { GetUserDataApi, ChangeUserDataApi } from "../request/api";
import "./less/Means.less";
import { useState } from "react";
import { LoadingOutlined, PlusOutlined } from "@ant-design/icons";

//base64
const getBase64 = (img, callback) => {
  const reader = new FileReader();
  reader.addEventListener("load", () => callback(reader.result));
  reader.readAsDataURL(img);
};
//
const beforeUpload = (file) => {
  const isJpgOrPng = file.type === "image/jpeg" || file.type === "image/png";

  if (!isJpgOrPng) {
    message.error("You can only upload JPG/PNG file!");
  }

  const isLt2M = file.size / 1024 / 1024 / 1024 < 200;

  if (!isLt2M) {
    message.error("200KB!");
  }

  return isJpgOrPng && isLt2M;
};

export default function Means() {
  const [loading, setLoading] = useState(false);
  const [imageUrl, setImageUrl] = useState("");
  useEffect(() => {
    GetUserDataApi().then((res) => {
      if (res.errCode === 0) message.success(res.message);
      sessionStorage.setItem("username", res.data.username);
    });
  }, []);

  //
  const onFinish = (values) => {
    console.log(values);
    if (
      values.username &&
      values.username !== sessionStorage.getItem("username") &&
      values.password.trim() !== ""
    ) {
      //
      ChangeUserDataApi({
        username: values.username,
        password: values.password,
      }).then((res) => {
        console.log(res);
      });
    }
  };

  //
  const handleChange = (info) => {
    if (info.file.status === "uploading") {
      setLoading(true);
      return;
    }

    if (info.file.status === "done") {
      // Get this url from response in real world.
      getBase64(info.file.originFileObj, (url) => {
        setLoading(false);
        setImageUrl(url);
        localStorage.setItem("avatar", info.file.response.data.filePath);
      });
    }
  };
  //
  const uploadButton = (
    <div>
      {loading ? <LoadingOutlined /> : <PlusOutlined />}
      <div
        style={{
          marginTop: 8,
        }}
      >
        Upload
      </div>
    </div>
  );
  return (
    <div className="means">
      <Form
        style={{ width: "400px" }}
        name="basic"
        onFinish={onFinish}
        // onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Form.Item
          label=""
          name="username"
          rules={[{ message: "!" }]}
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item label="   " name="password">
          <Input.Password placeholder="" />
        </Form.Item>

        <Form.Item
          wrapperCol={{
            offset: 8,
            span: 16,
          }}
        >
          <Button type="primary" htmlType="submit" style={{ float: "right" }}>
            
          </Button>
        </Form.Item>
      </Form>
      <p></p>
      <Upload
        name="avatar"
        listType="picture-card"
        className="avatar-uploader"
        showUploadList={false}
        action="/api/upload"
        beforeUpload={beforeUpload}
        onChange={handleChange}
        headers={{ "cms-token": localStorage.getItem("cms-token") }}
      >
        {imageUrl ? (
          <img
            src={imageUrl}
            alt="avatar"
            style={{
              width: "100%",
            }}
          />
        ) : (
          uploadButton
        )}
      </Upload>
    </div>
  );
}
